ype='button'],\r\ninput[type='reset'],\r\ninput[type='submit'] {\r\n  background-color: $purple;\r\n  border: .1rem solid $purple;\r\n  border-radius: .4rem;\r\n  color: $white;\r\n  transition:.25s;\r\n  cursor: pointer;\r\n  display: inline-block;\r\n  font-size: 1.1rem;\r\n  font-weight: 700;\r\n  height: 3.8rem;\r\n  letter-spacing: .1rem;\r\n  line-height: 3.8rem;\r\n  padding: 0 3.0rem;\r\n  text-align: center;\r\n  text-decoration: none;\r\n  text-transform: uppercase;\r\n  white-space: nowrap;\r\n  &:focus,\r\n  &:hover {\r\n    background-color: $orange;\r\n    border-color: $orange;\r\n    color: $white;\r\n    outline: 0;\r\n  }\r\n  &[disabled] {\r\n    cursor: default;\r\n    opacity: .5;\r\n    &:focus,\r\n    &:hover {\r\n      background-color: $purple;\r\n      border-color: $purple;\r\n    }\r\n  }\r\n  &.button-outline {\r\n    background-color: transparent;\r\n    color: $purple;\r\n    &:focus,\r\n    &:hover {\r\n      background-color: transparent;\r\n      border-color: white;\r\n      color: white;\r\n    }\r\n    &[disabled] &:focus,\r\n    &:hover {\r\n      border-color: inherit;\r\n      color: $purple;\r\n    }\r\n  }\r\n  &.button-clear {\r\n    background-color: transparent;\r\n    border-color: transparent;\r\n    color: $purple;\r\n    &:focus,\r\n    &:hover {\r\n      background-color: transparent;\r\n      border-color: transparent;\r\n      color: white;\r\n    }\r\n    &[disabled] &:focus,\r\n    &:hover \r\n        {color: $purple;}\r\n  }\r\n}\r\n","\r\n\r\n.toggle-button input\r\n{\r\n  display: block;\r\n  width: 40px;\r\n  height: 32px;\r\n  position: absolute;\r\n  top: -7px;\r\n  left: -5px;\r\n  \r\n  cursor: pointer;\r\n  \r\n  opacity: 0; /* hide this */\r\n  z-index: 2; /* and place it over the hamburger */\r\n  \r\n  -webkit-touch-callout: none;\r\n}\r\n\r\n/*\r\n * Just a quick hamburger\r\n */\r\n.toggle-button span\r\n{\r\n  display: block;\r\n  width: 23px;\r\n  height: 4px;\r\n  margin-bottom: 5px;\r\n  position: relative;\r\n  \r\n  background: $purple;\r\n  border-radius: 3px;\r\n  \r\n  z-index: 1;\r\n  \r\n  transform-origin: 4px 0px;\r\n  \r\n  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),\r\n              background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),\r\n              opacity 0.55s ease;\r\n}\r\n\r\n.toggle-button span:first-child\r\n{\r\n  transform-origin: 0% 0%;\r\n}\r\n\r\n.toggle-button span:nth-last-child(2)\r\n{\r\n  transform-origin: 0% 100%;\r\n}\r\n\r\n/* \r\n * Transform all the slices of hamburger\r\n * into a crossmark.\r\n */\r\n.toggle-button input:checked ~ span\r\n{\r\n  opacity: 1;\r\n  transform: rotate(45deg) translate(-2px, -1px);\r\n  background: $purple;\r\n  top:-15px;\r\n  width:33px;\r\n}\r\n\r\n/*\r\n * But let's hide the middle one.\r\n */\r\n.toggle-button input:checked ~ span:nth-last-child(3)\r\n{\r\n  opacity: 0;\r\n  transform: rotate(0deg) scale(0.2, 0.2);\r\n  width:33px;\r\n}\r\n\r\n/*\r\n * Ohyeah and the last one should go the other direction\r\n */\r\n.toggle-button input:checked ~ span:nth-last-child(2)\r\n{\r\n  transform: rotate(-45deg) translate(0, -1px);\r\n  top:11px;\r\n  width:33px;\r\n}\r\n\r\n\r\n\r\n/*\r\n * And let's slide it in from the left\r\n */\r\n.toggle-button input:checked ~ ul\r\n{\r\n  transform: none;\r\n}","dl,\r\nol,\r\nul {\r\n  list-style: none;\r\n  margin-top: 0;\r\n  padding-left: 0;\r\n  li {\r\n\tline-height:1.5;\r\n  }\r\n}\r\n\r\ndl,\r\nol,\r\nul {\r\n  font-size: 90%;\r\n  margin: 1.5rem 0 1.5rem 3.0rem;\r\n}\r\n\r\nol {\r\n  list-style: decimal inside;\r\n}\r\n\r\nul {\r\n  list-style: circle inside;\r\n}\r\n","body {\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n.MobileMenu {\r\n  ul {\r\n    transition:.25s;\r\n  }\r\n}\r\n\r\n.slideout-menu {\r\n  position: fixed;\r\n  top: 0;\r\n  bottom: 0;\r\n  width: 156px;\r\n  min-height: 100vh;\r\n  overflow-y: scroll;\r\n  -webkit-overflow-scrolling: touch;\r\n  z-index: 0;\r\n  background: $grey;\r\n  margin-top: 0;\r\n  padding: 15